/**
 * 将输入的时间戳转换为对应的「年月日」
 * @param timeStamp 输入的时间戳
 * @returns 返回时间戳对应的年月日 如：2022年11月17日
 */
export function getYearMonthDay(timeStamp: number): string {
    if (timeStamp) {
        var time = new Date(timeStamp);
        var y = time.getFullYear();
        var M = time.getMonth() + 1;
        var d = time.getDate();
        return y + '年' + M + '月' + d + '日';
    } else {
        return '';
    }
}
